import Vue from 'vue';
import { Form, FormItem, Input, Button, Alert } from 'element-ui';
import { digitUppercase } from 'utils/utils';
Vue.use(Form);
Vue.use(FormItem);
Vue.use(Input);
Vue.use(Button);
Vue.use(Alert);
export default Vue.extend({
    data() {
        return {
            form: {
                password: '123456'
            },
            rules: {
                password: [{ required: true, message: '需要支付密码才能进行支付' }]
            },
            submitting: false
        };
    },
    computed: {
        formData() {
            return this.$store.state.form.step;
        }
    },
    methods: {
        onValidateForm() {
            const formRef = this.$refs.form;
            formRef.validate((valid) => {
                if (valid) {
                    this.submitting = true;
                    this.$store
                        .dispatch('form/submitStepForm', Object.assign({}, this.form, this.formData))
                        .then(() => {
                        this.submitting = false;
                    })
                        .catch(() => {
                        this.submitting = false;
                    });
                }
            });
        },
        onPrev() {
            this.$router.push('/form/step-form');
        },
        digitUppercase(n) {
            return digitUppercase(n);
        }
    }
});



